:root {
  --background-color: #ffffff; /* Telegram light theme background */
  --text-color: #000000; /* Telegram light theme text */
  --button-bg-color: #0088cc; /* Telegram primary button color */
  --button-text-color: #ffffff; /* Telegram button text color */
  --tab-bar-bg-color: #f1f1f1; /* Telegram tab bar background */
}

@media (prefers-color-scheme: dark) {
  :root {
    --background-color: #1c1c1c; /* Telegram dark theme background */
    --text-color: #ffffff; /* Telegram dark theme text */
    --button-bg-color: #0088cc; /* Telegram primary button color */
    --button-text-color: #ffffff; /* Telegram button text color */
    --tab-bar-bg-color: #2a2a2a; /* Telegram tab bar background */
  }
}

.App {
  font-family: sans-serif;
  text-align: center;
  background-color: var(--background-color);
  color: var(--text-color);
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.tab-content {
  flex: 1;
  margin: 20px;
}

.grid {
  position: relative;
  top: 50%;
  transform: translate(0, 50%);
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  display: grid;
  gap: 10px;
}

.grid-2x2 {
  grid-template-columns: repeat(2, 1fr);
}

.grid-4x1 {
  grid-template-columns: 1fr;
}

.button {
  background-color: var(--button-bg-color);
  color: var(--button-text-color);
  padding: 10px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
}

.button:focus {
  outline: none;
}

.tab-bar {
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
  background-color: var(--tab-bar-bg-color);
  padding: 10px 0;
}

.tab-button {
  flex: 1;
  padding: 10px;
  border: none;
  background: none;
  font-size: 16px;
  color: var(--text-color);
}

.tab-button:focus {
  outline: none;
}
